.section {
  position: relative;
}

.section_features {
  padding: 60px 0;
  background-position: clamp(700px, 100%, 100%) 50% !important;
  background-repeat: no-repeat;
  background-size: auto 100% !important;
}

.section_count {
  padding: 60px 0;
  background-repeat: no-repeat;
  background-size: auto 100% !important;
}

.section_dealers {
  padding: 60px 0;
  background-repeat: no-repeat;
  background-size: auto 100%;
}

.section__content {
  max-width: clamp(700px, 40vw, 100%);
}

.section__content .title {
  margin-bottom: 40px;
}

.section__content .desc {
  overflow-wrap: break-word;
  font-size: 18px;
  padding-left: 10px;
  letter-spacing: 0;
  line-height: 1.5;
  margin-bottom: 2rem;
}

.section__content .desc .bignum {
  font-weight: 700;
  font-size: 3rem;
}

.section__content .desc .bigtext {
  font-weight: 700;
  font-size: 2rem;
}

.titlewhite .title {
  color: #fff;
}

.titleblue .title {
  color: #009ee3;
}

.textwhite .desc {
  color: #fff;
}

.textright {
  background-position: clamp(-700px, -1%, calc(100% - 700px)) 50% !important;
}

.textright .section__content {
  margin-left: auto;
}

.count {
  background-position: center;
  background-size: contain;
}

.angleleft-gray {
  position: relative;
}

.angleright-blue {
  position: relative;
}

.angleright-gray {
  position: relative;
}

.orange {
  color: #fff;
  background-color: #FC8356;
  background-size: 1100px auto !important;
  background-position: clamp(-1100px, -1%, calc(100% - 880px)) 50% !important;
}

.orange .title {
  color: white;
}

.wrap {
  display: grid;
  justify-items: center;
}

.section_dealers {
  background-position: center;
}

.section_dealers .title {
  color: white;
  margin-bottom: 59px;
}

.section_dealers .desc {
  color: white;
  font-size: 30px;
  max-width: 600px;
  padding-left: 10px;
